header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
}

header.black{
  background-color: #141414;
  transition: all ease 0.6s;
}

.header--logo{
  height: 25px;
}

.header--user {
  height: 35px;
}

.header--logo img{
  height: 100%;
}

.header--user img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  object-fit: cover;
}
